import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { BillingCategoryRoutingModule } from './billing-category-routing.module';
import { ListComponent } from './list/list.component';
import { AddComponent } from './add/add.component';
import { NgZorroAntdModule } from 'ng-zorro-antd';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';


@NgModule({
    declarations: [ListComponent, AddComponent],
    imports: [
        CommonModule,
        BillingCategoryRoutingModule,
        NgZorroAntdModule,
        ReactiveFormsModule,
        FormsModule,
    ],
    providers: [FormBuilderTypeSafe],
})
export class BillingCategoryModule { }
