import { Component, OnInit } from '@angular/core';
import { AkunBillingCategoryService } from 'src/app/sdk/hr/services';
import { Billing_category } from 'src/app/sdk/hr/models';
import { FilterFuctionService } from 'src/app/services/filter-fuction.service';
import { NzMessageService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

  constructor(
    private akunBillingCategoryService: AkunBillingCategoryService,
    private filterFuctionService: FilterFuctionService,
    private nzMessageService: NzMessageService,
    private spinner: NgxSpinnerService
) { }

ngOnInit(){
    this.getData()
}

listOfDisplayData: Billing_category[] = []
listOfData: Billing_category[] = []
getData() {
    this.spinner.show()
    this.akunBillingCategoryService.getAkunBillingCategories(JSON.stringify({sortKey: this.sortKey, sortValue: this.sortValue, search: this.search})).subscribe(
        data => {
            this.listOfDisplayData = data.data;
            this.listOfData = data.data;
            this.spinner.hide()
        }
    )
}


sortKey: string | null = null;
sortValue: string | null = null;
sort(sort: { key: string; value: string }): void {
    this.sortKey = sort.key;
    this.sortValue = sort.value;
    this.getData();
}
search: string = "";


delete(id)
{
    this.spinner.show();
    this.akunBillingCategoryService.deleteAkunBillingCategoriesId(id).subscribe(
        data => {
            this.getData();
            this.spinner.hide();
        }
    )
}

batalDelete()
{
    this.nzMessageService.info('click cancel');
}


}
