import { AkunBillingCategoryService } from 'src/app/sdk/hr/services';
import { Component, OnInit } from '@angular/core';
import { Billing_category } from 'src/app/sdk/hr/models';
import { NgxSpinnerService } from 'ngx-spinner';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private akunBillingCategoryService: AkunBillingCategoryService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router

    ) { }

    data: Billing_category

    ngOnInit() {
        this.form()
    }

    dataForm: FormGroupTypeSafe<Billing_category>
    form() {
        this.dataForm = this.fb.group<Billing_category>(
            {
                category: new FormControl('', [Validators.required]),
                keterangan: new FormControl('',)
            }
        )
    }
    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.data = this.dataForm.value;
        this.akunBillingCategoryService.postAkunBillingCategories(this.data).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/ga/account/pengaturan/billing-category/list'])
    }

}
